$tablet: 999px;
$mobile: 375px;

@mixin mediaTablet {
  @media screen and (max-width: $tablet) {
    @content;
  }
}

@mixin mediaMobile {
  @media screen and (max-width: $mobile) {
    @content;
  }
}



@mixin mediaHeight {
  @media screen and (min-height: $tablet) {
    @content;
  }
}
